import React from 'react';
import styled from 'styled-components';
import SplunkThemeProvider from '@splunk/themes/SplunkThemeProvider';

const Wrapper = styled.div`
    width: ${(props) => props.width || 360}px;
    height: ${(props) => props.height || 100}px;
    background-color: ${(props) => props.theme[props.backgroundColor]};
`;

const themeToVariant = {
    enterprise: { colorScheme: 'light', family: 'enterprise' },
    enterpriseDark: { colorScheme: 'dark', family: 'enterprise' },
    scp: { colorScheme: 'dark', family: 'prisma' },
};

export const themeDecorator = (themes) => (Story, context) => (
    <SplunkThemeProvider
        additionalThemeProperties={themes[context.args.themeId]}
        {...themeToVariant[context.args.themeId]}
    >
        <Story />
    </SplunkThemeProvider>
);

export const storySizeWrapper = (Story, context) => (
    <Wrapper
        height={context?.parameters?.storySize?.height}
        width={context?.parameters?.storySize?.width}
        backgroundColor={
            context?.parameters?.storySize?.backgroundColorKey ||
            'panelBackgroundColor'
        }
    >
        <Story />
    </Wrapper>
);
